    async function saveApplicationData(data_obj, action){
        if(!formMataData)
            setIsLoading(true);
        var subformName = tempBuffer[activeStepIndex]?.children[activeSubStepIndex]?.subformName ? tempBuffer[activeStepIndex]?.children[activeSubStepIndex]?.subformName : tempBuffer[activeStepIndex]?.subformName;        
        const applicationDataJson={
            "gdtoken":gdtoken,
            "witoken":witoken,
            "applicationId":openingAsApplication.applicationId,
            "applicationName": openingAsApplication.applicationName,
            "submitApplication": (data_obj && data_obj==="Y") || data_obj?.action==="close" ? "Y": "N",
            "stepIndex": activeStepIndex || "0",
            "stepSubIndex": activeSubStepIndex || "0",
            "isBackClicked": action==="back" ? true : false,
            "formName": subformName
        }
        const some_constant = await axios.post("/viewform/saveApplicationData",applicationDataJson,{withCredentials:true})
        .then((response)=>{
            if(response?.data?.error === "Y") {
                showAlertConfirmNotifications({
                    'open':true,
                    'showConfirm':'showNotification',
                    'alertType':'error',
                    'alertContentKey': `${t('There is some issue at server end. Kindly check with system administrator.')}`
                });
                setMoveToNextStep(false);
            }
            else {
                setMoveToNextStep(true);
            }
            if(response?.data?.APIData) {
                renderExecuteServerEventAPIData({outputArray: response?.data?.APIData, sync: true});
            }
            if(data_obj && data_obj === "Y"){
                if(response?.headers?.workitemlist !== undefined && response?.headers?.workitemlist !== ""){
                    showAlertConfirmNotifications({
                        'open':true,
                        'showConfirm':'showNotification',
                        'alertType':'success',
                        vertical: 'bottom', 
                        horizontal: 'left', 
                        'alertContentKey':`${response.headers.workitemlist} ${t("saved successfully")}`,
                        'autoHideDuration': 6000
                        });
                }
                if(data_obj?.action==="close" || data_obj?.action1==="close" || (data_obj &&  data_obj === "Y" ) ){
                    setCloseApplication_wizard(true);
                }
            }
            else if(data_obj?.action==="close" || data_obj?.action1==="close"){
                logoutFunc();
            }
            else if(data_obj && data_obj === "N"){
                setIsLoading(false);
            }            
        })
        .catch(err =>{
            setIsLoading(false)
        })        
    }

