    async function makeViewFormCall(formBufferJson, stepOrSubstep){
        var parentTable=[...formBufferJson?.TableList?.filter(item => Object.keys(item)[0]==="parenttable")][0];

        var tableList=[...formBufferJson?.TableList?.filter(item => Object.keys(item)[0]!=="parenttable")];

        var viewformJson={ 
            "isApp":"true",
            "DeviceType":"W",
            "isGeneralDataCreated":"false",
            "isWIModelCreated":"false",
            "gdtoken":gdtoken,
            "witoken":witoken,
            "pid":"Emp001",
            "wid":"1",
            "tid":"",
            "fid":"Emp001_1",
            "sessionInfo":"pre_1308210909-0000000014-process_sessionInfo",
            "formJson":JSON.stringify(formBufferJson),
            "isPreview":"false",
            "applicationName":openingAsApplication?.applicationName,
            "applicationId":openingAsApplication?.applicationId,
            "isNewSession":"false",
            "parentTable":JSON.stringify(parentTable),
            "tableList":JSON.stringify(tableList),
            "isLogin":islogin || openingAsApplication?.islogin || "N"
        }

        const viewformJson_filtered=await axios.post("/viewform/",viewformJson,{withCredentials:true})
                .then(respp => {
                        if(respp.data.status==="-2"){
                            setIsLoading(false);
                            showAlertConfirmNotifications({
                                'open':true,
                                'showConfirm':'showNotification',
                                'alertType':'error',
                                'alertContentKey': `${t("Invalid Login Information")}`});
                        }
                        else
                        {
                            if (props.previewmode && gdtoken && gdtoken !== "") {
                                var e = {
                                    type: "load",
                                };
                                if (
                                    respp.data?.formJson &&
                                    respp.data?.formJson.event &&
                                    respp.data?.formJson.event.Events &&
                                    respp.data?.formJson.event.Events.load
                                ) {
                                    triggerEventCalls(e, { id: "root" }, respp.data?.formJson?.event, respp.data?.formJson?.elem_type, () => { }, contexts);
                                }
                            }
                            setTransactionId(respp?.data?.TransactionId);
                            const formBufferFiltered=respp.data?.formJson?.children?.[1]?.children;
                            var dataVar=props.data;
                            if(stepOrSubstep==="step"){
                                dataVar.children[activeStepIndex].children=formBufferFiltered;
                            }
                            else if(stepOrSubstep==="substep"){
                                dataVar.children[activeStepIndex].children[activeSubStepIndex].children=formBufferFiltered;
                            }
                            updatedComponentInFormState(dataVar.id , {...dataVar});
                            if(builderMode==="application"){
                                const keyValue=JSON.stringify({"token":respp?.data?.Authorization || ""})
                                secureLocalStorage.setItem(window.isPortalLive ?  `launchpadKey_${applicationNameObj.applicationName}` : "launchpadKey", keyValue);
                            }

                            let SessionExpireWarnTime = respp.data?.SessionExpireWarnTime;
                            let SessionExpireWarnTimeMin;
                            let SessionExpireWarnTimeSec;
                            if(SessionExpireWarnTime){
                                SessionExpireWarnTime = parseInt(SessionExpireWarnTime);
                                if((typeof SessionExpireWarnTime) === 'number'){
                                    SessionExpireWarnTimeMin = Math.round(SessionExpireWarnTime / 60);
                                    SessionExpireWarnTimeSec = SessionExpireWarnTime % 60;
                                    
                                }
                            }
                            setSessionPopupTime({min:SessionExpireWarnTimeMin, sec:SessionExpireWarnTimeSec});

                            setFormBuillderMode("preview");    
                            var value=""
                            value=respp.data.witoken;
                            dispatch(updateStore({name:"witoken", value , key:props.containerId }));
                            value=respp.data.gdtoken;
                            dispatch(updateStore({name:"gdtoken", value , key:props.containerId}));
                            // 
                            setIsLoading(false)
                            return formBufferFiltered;
                        }
                })
                .catch(err => {
                    setIsLoading(false)
                })
        return viewformJson_filtered;
    }
